import { assestsUserAdmissionUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import style from "../PlateformUser/PlateformUser.module.css";
const SelectInstituteUniversalBatchCard = ({
  cls,
  selectedId,
  onSelect,
  selectList,
  isMultiSelect,
  onMultiSelect,
}) => {
  const onSelectStaff = () => {
    onSelect(cls);
  };
  const onSelectMultipleStaff = () => {
    onMultiSelect(cls);
  };
  return (
    <>
      {isMultiSelect ? (
        <div
          className={style.assign_show_list_each}
          onClick={onSelectMultipleStaff}
          style={{
            border: "0.5px solid #cacaca",
            marginBlock: "0.4rem",
            borderRadius: "6px",
            padding: "0.8rem 1rem",
          }}
        >
          {selectList?.includes(cls?._id) ? (
            <img
              src={`${assestsUserAdmissionUrl}/check.svg`}
              alt="icon"
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
            />
          ) : (
            <img
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
              src={`${assestsUserAdmissionUrl}/uncheck.svg`}
              alt="icon"
            />
          )}
          <div className={style.assign_each_paragraph}>
            <h6>{cls?.batchName ?? ""}</h6>
          </div>
        </div>
      ) : (
        <div
          className={style.assign_show_list_each}
          style={
            selectedId === cls?._id
              ? {
                  backgroundColor: "rgba(75, 26, 133, 0.1)",
                  border: "0.5px solid #cacaca",
                  marginBlock: "0.4rem",
                  borderRadius: "6px",
                  padding: "0.8rem 1rem",
                }
              : {
                  border: "0.5px solid #cacaca",
                  marginBlock: "0.4rem",
                  borderRadius: "6px",
                  padding: "0.8rem 1rem",
                }
          }
          onClick={onSelectStaff}
        >
          <div className={style.assign_each_paragraph}>
            <h6>{cls?.batchName ?? ""}</h6>
          </div>
        </div>
      )}
    </>
  );
};

export default SelectInstituteUniversalBatchCard;
