import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import {
  useAssignDesignationToStaff,
  useOneBatchDelete,
  useOneDepartmentSetCurrentBatch,
  useOneViewDepartment,
  useBatchLockUnlockAction,
  useRemoveDesignationToStaff,
} from "../DepartmentApi/depart-api";
import OptionWrapper from "../OptionWrapper";
import { useTranslation } from "react-i18next";
import QLoader from "../../../../Loader/QLoader";
import { useInstituteLinkUniversalBatch } from "../../../../optimized/Hooks/ApiHooks/DepartmentManager/department-manager-api";
import SelectInstituteUniversalBatch from "../../../../optimized/UserStaffMember/SelectInstituteUniversalBatch/SelectInstituteUniversalBatch";

const OneRowBatchOption = ({
  bid,
  setClick,
  editOneBatch,
  batchName,
  designation_send,
  batch_type,
  batchStatus,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [selectoptions, setSelectoptions] = useState([
    t("select_batch"),
    "Link With universal Batch",
    t("edit_"),
    t("delete"),
    "Remove Designation",
  ]);
  const ids = useSelector((state) => state.idChange);
  const [step, setStep] = useState("");

  const [setBatch] = useOneDepartmentSetCurrentBatch();
  const [disabled, setDisabled] = useState(false);
  const [batchDelete] = useOneBatchDelete();
  const [assignDesignationToStaff] = useAssignDesignationToStaff();
  const [removeDesignationToStaff] = useRemoveDesignationToStaff();
  const [batchLockUnlockAction] = useBatchLockUnlockAction();
  const [instituteLinkUniversalBatch] = useInstituteLinkUniversalBatch();

  const { oneDepartmentRefetch } = useOneViewDepartment({
    did: ids?.did,
    skip: false,
  });

  useEffect(() => {
    if (batch_type === "Identical" && designation_send === "No") {
      setSelectoptions([
        t("select_batch"),
        "Link With universal Batch",
        t("edit_"),
        t("delete"),
        "Assign Designation",
        "Remove Designation",
        batchStatus === "UnLocked" ? "Complete" : "Uncomplete",
      ]);
    } else {
      setSelectoptions([
        t("select_batch"),
        "Link With universal Batch",
        t("edit_"),
        t("delete"),
        "Remove Designation",
        batchStatus === "UnLocked" ? "Complete" : "Uncomplete",
      ]);
    }
  }, [designation_send, batch_type, batchStatus]);
  const selectBatchHandler = (option) => {
    if (option === "Select Batch") {
      setDisabled((pre) => !pre);
      setBatch({
        did: ids?.did,
        bid: bid,
        batch: "",
      })
        .then(() => {
          oneDepartmentRefetch();
          setDisabled((pre) => !pre);
          setClick(false);
        })
        .catch({});
    } else if (option === "Edit") {
      editOneBatch({
        batchName: batchName,
        bid: bid,
      });
      setClick(false);
    } else if (option === "Delete") {
      setDisabled((pre) => !pre);
      batchDelete({
        bid: bid,
      })
        .then(() => {
          oneDepartmentRefetch();
          setDisabled((pre) => !pre);
          setClick(false);
        })
        .catch({});
    } else if (option === "Assign Designation") {
      setDisabled((pre) => !pre);
      assignDesignationToStaff({
        bid: bid,
      })
        .then(() => {
          oneDepartmentRefetch();
          setDisabled((pre) => !pre);
          setClick(false);
        })
        .catch({});
    } else if (option === "Remove Designation") {
      setDisabled((pre) => !pre);
      removeDesignationToStaff({
        bid: bid,
      })
        .then(() => {
          oneDepartmentRefetch();
          setDisabled((pre) => !pre);
          setClick(false);
        })
        .catch({});
    } else if (option === "Link With universal Batch") {
      setStep("Link_Universal");
    } else if (option === "Complete" || option === "Uncomplete") {
      setDisabled((pre) => !pre);
      batchLockUnlockAction({
        bid: bid,
      })
        .then(() => {
          oneDepartmentRefetch();
          setDisabled((pre) => !pre);
          setClick(false);
        })
        .catch({});
    } else {
    }
  };
  const onCloseStep = () => {
    setStep("");
  };

  const onSelectUniversalBatch = (dt) => {
    if (bid && dt?._id) {
      setDisabled((pre) => !pre);
      onCloseStep();
      instituteLinkUniversalBatch({
        bid: dt?._id,
        bodyParameter: {
          link_batch: bid,
        },
      })
        .then(() => {
          oneDepartmentRefetch();
          setDisabled((pre) => !pre);
          setClick(false);
        })
        .catch({});
    }
  };

  return (
    <>
      {step === "Link_Universal" ? (
        <SelectInstituteUniversalBatch
          onSelectStaff={onSelectUniversalBatch}
          onClose={onCloseStep}
          selectedId=""
          instituteId={instituteId}
        />
      ) : (
        <OptionWrapper
          selectOptions={selectoptions}
          onClickOptionHandler={selectBatchHandler}
        />
      )}
      {disabled && <QLoader />}
    </>
  );
};

export default OneRowBatchOption;
